
<style scoped lang='scss'>
.datareport {
    width: 750px;
    height: 400px;
    -webkit-tap-highlight-color: transparent;
    user-select: none;
    position: relative;
    .sheetbox {
        position: relative;
    overflow: hidden;
    width: 750px;
    height: 400px;
    padding: 0px;
    margin: 0px;
    border-width: 0px;
    cursor: default;
        .sheet {
            position: absolute;
    left: 0px;
    top: 0px;
    width: 750px;
    height: 400px;
    user-select: none;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
    padding: 0px;
    margin: 0px;
    border-width: 0px;
        }
    }
}
</style>


<template>
    <div class="datareport">
        <el-card class="sheetbox" v-if="is_chart_data_init">
            <div class="sheet" ref='sheet'></div>
        </el-card>
    </div>
</template>


<script>
export default {
    name:'datareport',
    data() {
        return {
            chart_option:null,
            is_chart_data_init:false
        };
    },
    created(){

    },
    mounted(){
        this.getChartData()
    },
    //props:{},//[]
    methods:{
        set_echarts(it=this){
            let echarts=require('echarts')
            let myChart = echarts.init(it.$refs["sheet"]);
            let option =it.chart_option
            myChart.setOption(option)

        },
        async getChartData(){
            let {data:res}=await this.$http.get(
                `/reports/type/1`,{params:{}}
            )

            let {data:args}=res
            console.log(args)
            this.chart_option=args
              
            this.chart_option.title={
            text: '用户来源'
            }
            this.chart_option.tooltip={
                trigger: 'axis',
                axisPointer: {
                    type: 'cross',
                    label: {
                    backgroundColor: '#E9EEF3'
                    }
                }
            }
            this.chart_option.grid= {
                left: '3%',
                right: '4%',
                bottom: '3%',
                containLabel: true
            }
            this.chart_option.xAxis.push( {
                boundaryGap: false
            })
            console.log(this.chart_option)
            this.is_chart_data_init=true
            this.$nextTick(()=>{
                this.set_echarts()
            })
        }
    },
    computed:{

    },
    watch:{

    },
    filters:{

    },
    directives:{

    },
    components:{

    },
};
</script>